.App {
  text-align: center;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #ffdffa;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.modal {
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  background-color: white;
  padding: 1rem;
  text-align: center;
  width: 30rem;
  z-index: 10;
  position: fixed;
  top: 20vh;
  left: calc(50% - 15rem);
  max-height: calc(100vh - 210px);
  overflow-y: auto;
}

.modalb {
  box-shadow: 0 1px 4px rgb(0 0 0 / 20%);
    border-radius: 6px;
    background-color: rgb(5, 5, 5);
    padding: 1rem;
    text-align: center;
    width: 8rem;
    z-index: 10;
    position: fixed;
    top: 9vh;
    right: calc(50% - 34rem);
    max-height: calc(100vh- 210px);
}



.modal > button {
  background-color: #000000;
  margin: 5px;
  color:rgb(255, 255, 255);
}
.backdrop {
  position: fixed;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.75);
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

.modal-button {
  border: rgb(255, 170, 170) solid 1px;
  color: rgb(255, 255, 255);
  margin: 10px;
  background-color: rgb(255, 170, 170);
}

.modal-button-b {
  background-color: white;
  border: rgb(255, 170, 170) solid 1px;
  color: rgb(255, 170, 170);
}

.modal-button:hover {
  color: #61dafb;
}

.modal-button-b:hover {
  color: #61dafb;
}

.formRegister {
  margin-left: 152px;
  display: table-caption;
  text-align: start;
  color: black;
}

h1 {
  color: black;
}

.productsSelection {
  justify-content: space-around;
}

.modal > p {
  color: black;
}
